import AppBar from '@material-ui/core/AppBar';
import ReactMarkdown from 'react-markdown';

class Skills extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      strapiUrl: this.props.strapiUrl,
      classes: this.props.classes,
    };
  }

  getSkillsByDomain = (domain) => {
    const domainSkills = this.props.skills.filter(skill => {
      return skill.domaine._id == domain
    });

    return <div>
      {domainSkills.map(skill => (
        <div key={skill._id} className={skill.Acquis ? this.state.classes.skillok : null}>
          <h3>{skill.Titre}</h3>
          <img src={this.state.strapiUrl + skill.Logo.url} alt={skill.Titre} />
        </div>
      ))}
    </div>
  };

  render() {
    const { classes, hp, lang, domains } = this.props;

    return (
      <div id="section-skills">
        <AppBar position="static" className={classes.appbar}>
          {hp['Titre compétences']}
        </AppBar>
        <ReactMarkdown source={hp['Texte compétences']} className={classes.markdownskills} />
        {domains.map(domain => (
          <div key={domain._id}>
            <h2>{lang === 'en' ? domain['Titre anglais'] : domain.Titre}</h2>
            <div>{this.getSkillsByDomain(domain._id)}</div>
          </div>
        ))}
      </div>
    );
  }
}

export default Skills;
